<template>
  <div class="">
    <h1>Loaders</h1>

    <h2>Loader</h2>
    <!-- <Loader></Loader> -->

    <h2>Page Loader</h2>
    <div class="page-loader" v-if="!isloaded">
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
    </div>
    <PageLoader v-else-if="isloaded == true"></PageLoader>

    <h2>Skeleton Loader</h2>
    <SkeletonLoader></SkeletonLoader>
  </div>
</template>



<script>
import SkeletonLoader from "./views/SkeletonLoader.vue";
import PageLoader from "./views/PageLoader.vue";
// import Loader from './views/Loader.vue'
// import * as d3 from 'd3'
export default {
  name: "Loaders",
  components: {
    SkeletonLoader,
    PageLoader,
    // Loader
  },
  data: () => {
    return {
      isloaded: false,
      //  loaded: false,
    };
  },
  mounted() {
    document.onreadystatechange = () => {
      if (document.readyState == "complete") {
        this.isloaded = true;
      }
    };
  },
};
</script>

